//--
// Imports
@import 'config/variables';
@import 'config/mixins';

//--
// Styles
* {
    box-sizing: border-box;
}
body {
    width: 90%;
    margin: 0 auto;
    padding: 0;
    background-color: #008080;
    outline: none;
}

$headers: 'h1,h2,h3,h4,h5,h6';
body, input, textarea, p > span, button, #{$headers}, form, div, ul, li, th, td, dl, dt, dd, ol, pre, blockquote {
    direction: ltr;
    color: #fff;
}
p, a, button, label, ul, li, ol, pre, blockquote {
    font-family: 'Open Sans', sans-serif;
    font-size: 16px
}
#{$headers} {
    word-spacing: 3px;
    &, p > span {
        font-family: 'Ubuntu Condensed', sans-serif;
    }
}
main {
    padding: 100px 0 60px 0;
}
input, .example, textarea.question {
    font-family: 'Ubuntu Mono', monospace;
}

*,
*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
:focus {
    outline: none;
}
a {
    text-decoration: none;
    color: #e7da61;/*#f0e68c;*/
    /*text-decoration: underline dotted;*/
    -webkit-transition: color 0.5s;
    -moz-transition: color 0.5s;
    -o-transition: color 0.5s;
    transition: color 0.5s;
    &:hover, &:focus, &:active {
        color: #f3eba3;
    }
    &:after {
        color: #e7da61
    }
}

#fullimg li, #nytimes-articles li {
    list-style-type: none;
}

// Containers
@include keyframe(opac) {
    0% {opacity: 0}
    to {opacity: 1}
}
#bgimg, #fullimg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 1;
    @include animation(opac, .5s, linear)
}
#bgimg {
    z-index: -1
}
#fullimg {
    z-index: 12;
    li {
        margin: 0 15px;
        display: inline-block
    }
}
#bti {
    display: none;
    border: 1px solid transparent;
    border-top-color: #fff
}
#map-canv {
    width:100%;
    height:300px
}
.form {
    &-container {
        margin-top: 100px;
        border-radius: 1px;
        &, #prompts {
            width: 60%
        }
    }
    &-line {
        display: block
    }
}
.refs {
    margin-bottom: 10px;
    display: block
}
.inp-text, .labels {
    display: inline-block
}

.greeting {
    h2 {
        padding-top: 20px
    }
    p {
        padding-left: 10px;
        text-align: left
    }
    &, .subm {
        text-align: center   
    }
}
.greeting p {
    
}
.greeting, .subm {  
    text-align: center
}
.subm {
    width: 100%
}
.main_txt p:not(.img_art) {
    text-indent: 1.5em
}
.art_small {
    text-align: center;
    img {
        width: 50%;
    }
}
.labels {
    width: 65px;
}

// Prompts
#prompts {
    position: bottom 50%;
    padding: 10px;
    background-color: rgba(0, 76, 76, 0.7);
    -webkit-box-shadow: 1px 1px 7px #222 inset;
    overflow-y: scroll;
    overflow-x: hidden;
    z-index: 4;
    &-more {
        position: absolute
    }
    h3 {
        text-align: center
    }
}

@include keyframe(panim) {
    0% {
        opacity: 0;
        transform: translateY(-70px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
.example {
    color: #bbb;
}

// close icons
%img_plh {
    width: 40px;
    cursor: pointer
}
#close {
    &-p, &-im, &-f {
        position: top right;
        top: 50px;
        margin: 0 15px;
        text-align: center;
        z-index: 7;
        img {
            @extend %img_plh;
        }
    }
}
#fullimg img:last-child {
    @extend %img_plh;
}
body::-webkit-scrollbar, #prompts::-webkit-scrollbar {
    width: 0.5em;
}
body::-webkit-scrollbar-track, #prompts::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0);
    color: #fff;
}
body::-webkit-scrollbar-thumb, #prompts::-webkit-scrollbar-thumb {
    background-color: #e7da61;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
    outline: none;
}
#alert-b, .warning {
    border-radius: 10px
}
#alert-b {
    position: fixed;
    display: none;
    bottom: 0;
    width: 70%;
    margin: 0 auto;
    padding: 10px 20px;
    opacity: 0;
    background-color: #b42800;
    box-shadow: 12px 0 0 solid #000;
    text-align: center;
    -webkit-animation: ale 10s;
}
.warning {
    text-align: center
}
.warning label {
    padding: 10px
}
@-webkit-keyframes ale {
    0% {opacity: 1; -webkit-transform: translateY(80px);}
    10%, 90% {opacity: 1; -webkit-transform: translateY(0);}
    100% {opacity: 1; -webkit-transform: translateY(80px);}
}
@keyframes ale {
    0% {opacity: 1; transform: translateY(80px);}
    10%, 90% {opacity: 1; transform: translateY(0);}
    100% {opacity: 1; transform: translateY(80px);}
}

/* Articles */
.articles {
    margin-top: 15px;/*border-top: 2px solid #00bfff;*/
    padding: 10px;
    border-top: 1px solid #bbb;
}
.article:first-child {
    border-top: none;
}
#nytimes-articles {
    margin: 0;
    padding: 0;
}
.form-container, .articles {
    background-color: rgba(0, 76, 76, 0.7);
}
.wikipedia-container, .nytimes-container {
    margin: 0;
    padding: 10px;
}
#wikipedia-links {
    -webkit-padding-start: 0;
}
.nytimes-container {
    border: none;
    border-left: 1px solid #fff;
}
.head_s {
    line-height: 0.3;
    text-align: center;
}
.head_s img {
    max-width: 90%;
    border-radius: 10px;
}
.illustration {
   width: 100%;
   text-align: center
}
.social_c {
    width: 50%;
}
.social_c div {
    text-align: center
}
.social_c img {
    width: 90px;
    padding: 0 15px;
    cursor: pointer;
}

/* Footer */
footer {
    position: fixed;
    margin: 15px 0 0 0;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #004242;
    border: none;
    border-top: 1px solid #fff;
    text-align: center;
}
footer p {
    line-height: 0.3;
    font-size: 12px;
}

/* Input fields */
/* necessary to give position: relative to parent. */
input,
span,
label,
textarea {
    padding: 5px;
    border: none;
}
textarea:focus,
input:focus {
    outline: 0;
}

/* Question, buttons */
div > button {
    text-align: center;
}
button {
    border: none;
    margin: 10px 0;
    padding: 10px 20px;
    font-size: 20px;
    text-align: center;
    cursor: pointer;
    -webkit-transition: background-color .5s
}
button, .input_wrapper input[type="checkbox"] {
    background-color: rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: 1px 1px 5px #222 inset;
}
button, .form-container, .articles, #prompts, #prompts::-webkit-thumb, .input_wrapper input[type="checkbox"], .input_wrapper input[type="checkbox"]:after {
    border-radius: 10px;
}
button:focus, button:hover, button:active {
    background-color: rgba(0, 0, 0, 0.5);
}
button:after {
    background-color: rgba(0, 0, 0, 0.2);
}
.btn-subm {
    width: 100%;
    display: block;
    text-align: center;
}

/* Textarea and input */
input.question,
textarea.question {
    margin: 0;
    border: none;
    background: rgba(0, 0, 0, 0);
    transition: padding-top 0.2s ease, margin-top 0.2s ease;
    overflow-x: hidden; /* Hack to make "rows" attribute apply in Firefox. */
}
input.question:not(.report) {
    width: 70%;
}
textarea.question {
    resize: vertical
}

/* Autofills. It is works when user selects the newly entered values for the fields from the drop-down list. */
input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 1000px #f0e68c inset;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    transition: background-color 5000s ease-in-out 0s;
}

/* Underline and Placeholder */
::-webkit-input-placeholder {color: #bbb;}
::-moz-placeholder {color: #bbb;}
:-ms-input-placeholder {color: #bbb;}
:-moz-placeholder {color: #bbb;}
input.question + label, textarea.question + label {
    display: block;
    position: relative;
    white-space: nowrap;
    padding: 0;
    margin: 0;
    border-top: 1px solid red;
    -webkit-transition: width 0.4s ease;
    transition: width 0.4s ease;
    height: 0px;
}
input.question + label {
    width: 40%;
}
textarea.question + label {
    width: 100%;
}
input.question:focus + label, textarea.question:focus + label {
    width: 100%
}
textarea.question:valid, textarea.question:focus {
    margin-top: 20px;
}
input.question:focus + label > span, input.question:valid + label > span {
    top: -100px;
    font-size: 22px;
}
textarea.question:focus + label > span, textarea.question:valid + label > span {
    top: -150px;
    font-size: 22px;
}
input.question:valid + label, textarea.question:valid + label {
    border-color: #fff;
    background-color: rgba(255, 255, 255, 0.5);
}
input.question:invalid, textarea.question:invalid {
    -webkit-box-shadow: none;
}
input.question + label > span, textarea.question + label > span {
    margin: 0;
    position: absolute;
    color: #8f8f8f;
    font-size: 48px;
    top: -66px;
    left: 0px;
    z-index: -1;
    -webkit-transition: top 0.2s ease, font-size 0.2s ease, color 0.2s ease;
    transition: top 0.2s ease, font-size 0.2s ease, color 0.2s ease;
}